.all-search {
  width: 3.75rem;
  padding: 0.2rem 0.15rem 1rem;
  box-sizing: border-box;
}
.all-search .am-tag-normal {
  width: 1.65rem;
  height: 0.4rem;
  border-radius: 0.02rem;
  background-color: #ffffff;
  border: 0.01rem solid #dddddd;
  float: left;
  line-height: 0.4rem;
  margin-right: 0.15rem;
  text-align: center;
  color: #333333;
  font-size: 0.14rem;
  margin-bottom: 0.2rem;
}
.all-search .am-tag-active {
  width: 1.65rem;
  height: 0.4rem;
  border-radius: 0.02rem;
  background-color: #dba131;
  border: 0.01rem solid #dba131;
  float: left;
  line-height: 0.4rem;
  margin-right: 0.15rem;
  text-align: center;
  color: #fff;
  font-size: 0.14rem;
  margin-bottom: 0.2rem;
}
.all-search .am-tag:nth-child(even) {
  margin-right: 0;
}
.all-search .search-foot {
  width: 3.45rem;
  height: 0.55rem;
  position: fixed;
  left: 0;
  bottom: 0;
  text-align: center;
  padding: 0.08rem 0.15rem 0.08rem;
  background: #fff;
  box-shadow: 0px -1px 4px 0px #dddddd;
}
.all-search .search-foot .btn {
  float: left;
  width: 1.64rem;
  height: 0.4rem;
  border-radius: 0.04rem;
  background-color: #ffffff;
  color: #dba131;
  line-height: 0.4rem;
  font-size: 16px;
  margin-right: 0.15rem;
}
.all-search .search-foot .btn.check {
  border-radius: 4px;
  background-color: #dba131;
  color: #fff;
  margin-right: 0;
}
